import { useMemo } from "react";
import { useTranslation } from "react-i18next";

import InnerRoutes from "./Routes";
import classes from "./Architector.module.scss";
import { Container, PageMenu, PageTitle } from "components/shared";

const Contractor = () => {
	const { t } = useTranslation();

	const menus = useMemo(
		() => [
			{ href: "/architector/complexes", label: t("Мажмуалар"), isActive: false },
			{ href: "/architector/buildings", label: t("Бинолар"), isActive: false },
			{ href: "/architector/apartments", label: t("Хонадонлар"), isActive: false }
		],
		[t]
	);

	return (
		<div className={classes.main}>
			<Container>
				<PageTitle title={t("Мени келажакдаги уйим")} />

				<PageMenu menus={menus} />

				<InnerRoutes />
			</Container>
		</div>
	);
};

export default Contractor;
